<script setup>
import {onMounted,ref} from 'vue';
import router from 'router';
// 邮箱登录
import EMLogin from 'views/login/login/EMLogin.vue';
// 短信登录
import SMSLogin from 'views/login/login/SMSLogin.vue';
// 密码登录
import PasswordLogin from 'views/login/login/PasswordLogin.vue';
// 信息提示
import Alert from 'views/login/login/Alert.vue';
// 二维码登录
import QRcodeLogin from 'views/login/login/QRcodeLogin.vue';
// 路由跳转
const push = (link)=>{
    router.push(link)
}
// 号码登录 导航栏内容
var loginsetlect = ref([
    {name:"邮箱登录",show:true,style:"border-bottom: 2px solid #000;color: #000;"},
    {name:"密码登录",show:false,style:undefined},
    {name:"短信登录",show:false,style:undefined}
])
// 号码登录 导航栏点击事件
const loginsetlectclick = (i)=>{
    for (let s = 0; s < 3; s++) {
        if (s==i) {
            // 更换背景色
            loginsetlect.value[s].show = true; 
            loginsetlect.value[s].style = "border-bottom: 2px solid #000;color: #000;"; 
        } else {
            loginsetlect.value[s].show = false; 
            loginsetlect.value[s].style = undefined; 
        }
    }
    
}
// 弹窗内容
var alertnum = ref()
var alert = ref()
// 自动登录图标
var nextloginiocncolor = ref('#ffffff')
// 是否自动登录
var nextlogin = ref(true)
// 点击自动登录时事件处理
const nextloginiocnclick = ()=>{
    if (nextlogin.value == true) {
        nextlogin.value = false;
        nextloginiocncolor.value = "#6699FF";
    } else if(nextlogin.value == false){
        nextlogin.value = true;
        nextloginiocncolor.value = "#ffffff";
    }
}
// 登录盒子号码登录或者二维码登录
var loginboxSMSorQR = ref(true)
// 点击 登录盒子号码登录或者二维码登录 图标 事件
var loginboxSMSorQRclick = ()=>{
    loginboxSMSorQR.value = !loginboxSMSorQR.value;
}
// 二维码登录点击导航 密码登录
const QRcodeToPwloign = ()=>{
    loginboxSMSorQR.value = true;
    loginsetlectclick(1);

}
// 登录组件返回的提示信息
const EMLoginAlert = (data)=>{
    alert.value=data
}
const EMLoginAlertNum = (data)=>{
    alertnum.value=data
}
onMounted(() => {

})
</script>
